export default {
  namespaced: true,
  state: {
    pointer: {
      x: 0,
      y: 0,
    },
    canvas: null,
  },
  getters: {
    pointer(state) {
      return state.pointer;
    },
    canvas(state) {
      return state.canvas;
    },
  },
  mutations: {
    setPointer(state, translate) {
      state.pointer = translate;
    },

    setCanvas(state, svg) {
      state.canvas = svg;
    },
  },
  actions: {},
};
