<template>
  <path
    :d="`M ${startPoint.x} ${startPoint.y} L ${middlePoint.x} ${middlePoint.y} L ${endPoint.x} ${endPoint.y} `"
    :style="cssStyle"
    :marker-start="`url(#${markerStart})`"
    :marker-mid="`url(#${markerMid})`"
    :marker-end="`url(#${markerEnd})`"
  ></path>
</template>

<script>
export default {
  props: {
    startPoint: {
      type: Object,
      required: true,
    },
    endPoint: {
      type: Object,
      required: true,
    },
    markerStart: {
      type: String,
    },
    markerMid: {
      type: String,
    },
    markerEnd: {
      type: String,
    },
    cssStyle: {
      type: Object,
      default: () => {
        return {
          fill: "transparent",
          stroke: "#2856FF",
          strokeWidth: 2,
        };
      },
    },
  },
  computed: {
    middlePoint() {
      return {
        x: (this.endPoint.x - this.startPoint.x) / 2 + this.startPoint.x,
        y: (this.endPoint.y - this.startPoint.y) / 2 + this.startPoint.y,
      };
    },
  },
};
</script>

<style lang="less" scoped></style>
