<template>
  <g>
    <path
      :d="`M ${startPoint.x} ${startPoint.y} H ${middlePoint.x}`"
      :style="cssStyle"
      :marker-start="`url(#${markerStart})`"
    ></path>
    <path
      :d="`M ${middlePoint.x} ${startPoint.y} V ${endPoint.y} `"
      :style="cssStyle"
      :marker-mid="`url(#${markerMid})`"
    ></path>
    <path
      :d="`M ${middlePoint.x} ${endPoint.y} H ${endPoint.x} `"
      :style="cssStyle"
      :marker-end="`url(#${markerEnd})`"
    ></path>
  </g>
</template>

<script>
export default {
  props: {
    startPoint: {
      type: Object,
      required: true,
    },
    endPoint: {
      type: Object,
      required: true,
    },
    startNode: {
      type: String,
    },
    endNode: {
      type: String,
    },
    markerStart: {
      type: String,
    },
    markerMid: {
      type: String,
    },
    markerEnd: {
      type: String,
    },
    cssStyle: {
      type: Object,
      default: () => {
        return {
          fill: "transparent",
          stroke: "#2856FF",
          strokeWidth: 2,
        };
      },
    },
  },
  computed: {
    middlePoint() {
      return {
        x: (this.endPoint.x - this.startPoint.x) / 2 + this.startPoint.x,
        y: (this.endPoint.y - this.startPoint.y) / 2 + this.startPoint.y,
      };
    },
  },
};
</script>

<style lang="less" scoped></style>
