<template>
  <g v-show="show">
    <!-- 内容 -->
    <rect
      :width="target.size.width"
      :height="target.size.height"
      :style="maskStyle"
      :transform="`translate(${position.x}, ${position.y})`"
      @mousedown="MaskHandler"
    >
    </rect>
    <!-- 旋转 -->
    <!-- 四角变换 -->
    <rect
      style="cursor: nw-resize"
      :width="scaleNode.width"
      :height="scaleNode.height"
      :style="scaleNode.style"
      :transform="`translate(${leftTop.x}, ${leftTop.y})`"
    >
    </rect>
    <rect
      style="cursor: ne-resize"
      :width="scaleNode.width"
      :height="scaleNode.height"
      :style="scaleNode.style"
      :transform="`translate(${rightBottom.x}, ${leftTop.y})`"
    >
    </rect>
    <rect
      style="cursor: se-resize"
      :width="scaleNode.width"
      :height="scaleNode.height"
      :style="scaleNode.style"
      :transform="`translate(${rightBottom.x}, ${rightBottom.y})`"
    >
    </rect>
    <rect
      style="cursor: sw-resize"
      :width="scaleNode.width"
      :height="scaleNode.height"
      :style="scaleNode.style"
      :transform="`translate(${leftTop.x}, ${rightBottom.y})`"
    >
    </rect>
    <!-- 锚点 -->
  </g>
</template>

<script>
export default {
  props: {
    show: {
      type: Boolean,
      default: true,
    },
    target: {
      type: Object,
      default: () => {
        return {
          translate: { x: 50, y: 50 },
          scale: { x: 1, y: 1 },
          rotate: 0,
          anchor: { x: 25, y: 25 },
          size: { width: 50, height: 50 },
        };
      },
    },
  },
  data() {
    return {
      scaleNode: {
        width: 10,
        height: 10,
        style: {
          stroke: "black",
          strokeWidth: 1,
          fill: "white",
        },
      },
      maskStyle: {
        stroke: "#1584FC",
        strokeWidth: 2,
        fill: "transparent",
        cursor: "move",
      },
      dragging: false,
      changed: false,
    };
  },
  computed: {
    position() {
      return {
        x: this.target.translate.x - this.target.anchor.x,
        y: this.target.translate.y - this.target.anchor.y,
      };
    },
    halfScaleNodeSize() {
      return {
        width: this.scaleNode.width / 2,
        height: this.scaleNode.height / 2,
      };
    },
    leftTop() {
      return {
        x: this.position.x - this.halfScaleNodeSize.width,
        y: this.position.y - this.halfScaleNodeSize.height,
      };
    },
    rightBottom() {
      return {
        x:
          this.position.x -
          this.halfScaleNodeSize.width +
          this.target.size.width,
        y:
          this.position.y -
          this.halfScaleNodeSize.height +
          this.target.size.height,
      };
    },
    canvas() {
      return this.$store.getters["canvas/canvas"];
    },
  },
  methods: {
    MaskHandler() {
      this.dragging = true;
      this.changed = true;
      const mousemoveFun = (event) => {
        this.target.translate.x = event.offsetX;
        this.target.translate.y = event.offsetY;
      };

      const mouseupFun = () => {
        this.dragging = false;
        this.canvas.removeEventListener("mousemove", mousemoveFun);
        document.body.removeEventListener("mouseup", mousemoveFun);
      };

      this.canvas.addEventListener("mousemove", mousemoveFun);
      document.body.addEventListener("mouseup", mouseupFun);
    },
  },
};
</script>

<style lang="less" scoped></style>
