<template>
  <circle
    :r="configure.r"
    :style="cssStyle"
    :transform="`translate(${offsetDistance.x}, ${offsetDistance.y})`"
    @mousedown="mousedown('p1')"
    @mouseup="mouseup('p1')"
  ></circle>
</template>

<script>
export default {
  props: {
    node: {
      type: String,
      required: true,
    },
    position: {
      type: Object,
      required: true,
    },
    size: {
      type: Object,
      required: true,
    },
    configure: {
      type: Object,
      default: () => {
        return {
          r: 5,
          offset: {
            x: 0.5,
            y: 1.5,
          },
        };
      },
    },
    cssStyle: {
      type: Object,
      default: () => {
        return {
          fill: "white",
          stroke: "blue",
          strokeWidth: 1,
          cursor: "crosshair",
        };
      },
    },
  },
  data() {
    return {
      interface: ["p1"],
    };
  },

  computed: {
    cacheLine() {
      return this.$store.getters["structure/getCacheLine"];
    },

    offsetDistance() {
      return {
        x: this.size.width * this.configure.offset.x,
        y: this.size.height * this.configure.offset.y,
      };
    },

    p1() {
      return {
        x: this.position.x + this.offsetDistance.x,
        y: this.position.y + this.offsetDistance.y,
      };
    },
  },

  methods: {
    mousedown(point) {
      console.log(this.node, point, this.p1);
      this.cacheLine.start = {
        node: this.node,
        interface: "p1",
        position: { ...this.p1 },
      };
    },
    mouseup(point) {
      console.log(this.node, point, this.p1);
      this.cacheLine.end = {
        node: this.node,
        interface: "p1",
        position: { ...this.p1 },
      };
      this.$store.commit("structure/generateLine");
    },
  },
};
</script>

<style lang="less" scoped></style>
