<template>
  <g>
    <circle
      :r="point.r"
      :style="point.style"
      :transform="`translate(${startNode.x}, ${startNode.y})`"
      class="connectLine"
    ></circle>

    <defs>
      <component
        :is="framework.markerStart"
        :id="id"
        v-bind="markerStart"
      ></component>

      <component
        :is="framework.markerMid"
        :id="id"
        v-bind="markerMid"
      ></component>

      <component
        :is="framework.markerEnd"
        :id="id"
        v-bind="markerEnd"
      ></component>
    </defs>

    <component
      :is="framework.shape"
      :markerStart="`${id}${framework.markerStart}`"
      :markerMid="`${id}${framework.markerMid}`"
      :markerEnd="`${id}${framework.markerEnd}`"
      :startPoint="startPoint"
      :endPoint="endPoint"
      :startNode="start.node"
      :endNode="end.node"
      v-bind="shape"
    ></component>

    <circle
      :r="point.r"
      :style="point.style"
      :transform="`translate(${endNode.x}, ${endNode.y})`"
      class="connectLine"
    ></circle>
  </g>
</template>

<script>
import svg from "../../mixins/svg";
import line from "../../mixins/line";
import dev from "../../mixins/dev";

import { components as shapeComponents } from "./shape";
import { components as widgetComponents } from "./widget";

export default {
  mixins: [svg, line, dev],
  components: {
    ...shapeComponents,
    ...widgetComponents,
  },
  props: {
    framework: {
      type: Object,
      default: () => {
        return {
          shape: "linear",
          markerStart: "",
          markerMid: "textWidget",
          markerEnd: "arrow",
        };
      },
    },
    shape: {
      type: Object,
      required: true,
    },
    markerStart: {
      type: Object,
    },
    markerMid: {
      type: Object,
    },
    markerEnd: {
      type: Object,
    },
  },
  data() {
    return {
      point: {
        r: 10,
        style: {
          fill: "transparent",
          stroke: "green",
          strokeWidth: 1,
        },
      },
      startPoint: { x: 0, y: 0 },
      endPoint: { x: 0, y: 0 },
    };
  },
  computed: {
    startNode() {
      const node = this.$store.getters["component/getNode"](this.start.node);
      if (node && node.$refs.connector[this.start.interface]) {
        return node.$refs.connector[this.start.interface];
      }
      return this.start.position;
    },
    endNode() {
      const node = this.$store.getters["component/getNode"](this.end.node);
      if (node && node.$refs.connector[this.end.interface]) {
        return node.$refs.connector[this.end.interface];
      }
      return this.end.position;
    },
  },
  watch: {
    startNode: {
      handler(newVal, oldVal) {
        if (newVal !== oldVal) {
          if (newVal !== this.start.position) {
            this.start.position.x = newVal.x;
            this.start.position.y = newVal.y;
          }

          this.startPoint = newVal;
        }
      },
      immediate: true,
    },
    endNode: {
      handler(newVal, oldVal) {
        if (newVal !== oldVal) {
          if (newVal !== this.end.position) {
            this.end.position.x = newVal.x;
            this.end.position.y = newVal.y;
          }
          this.endPoint = newVal;
        }
      },
      immediate: true,
    },
  },
  methods: {
    mouseenter() {
      console.log(this.id);
    },
  },
  mounted() {},
};
</script>

<style lang="less" scoped>
.connectLine {
  opacity: 0;
  transition: all ease 500ms;
  &:hover {
    opacity: 1;
  }
}
</style>
