export default {
  namespaced: true,
  state: {
    node: "",
    line: "",
  },
  getters: {
    node(state) {
      return state.node;
    },
  },
  mutations: {
    setNode(state, id) {
      const structure = this.getters["structure/getNode"](id);
      state.node = structure;
    },

    clearNode(state) {
      state.node = "";
      state.connector = "";
    },
  },
  actions: {},
};
