import merge from "webpack-merge";
import { components as connectorComponents } from "./connector";
import { components as shapeComponents } from "./shape";
import basicShape from "./theme/basicShape";
import squareText from "./theme/squareText";

import { components as widgetComponents } from "./widget";

import node from "./node.vue";
import { getComponentPropStructure } from "../../assets/js/utils";

const mergeNodeStructure = function (inventory) {
  // 合并节点基础配置
  const structure = merge(getComponentPropStructure(node), inventory.node);
  structure.framework = {};

  structure.framework.connector = inventory.connector.component;
  if (inventory.connector.component) {
    structure.connector = merge(
      getComponentPropStructure(
        connectorComponents[inventory.connector.component]
      ),
      inventory.connector.props
    );
  }

  structure.framework.widget = inventory.widget.component;
  if (inventory.widget.component) {
    structure.widget = merge(
      getComponentPropStructure(widgetComponents[inventory.widget.component]),
      inventory.widget.props
    );
  }

  structure.framework.shape = inventory.shape.component;
  if (inventory.shape.component) {
    structure.shape = merge(
      getComponentPropStructure(shapeComponents[inventory.shape.component]),
      inventory.shape.props
    );
  }

  structure.name = inventory.name;
  structure.label = inventory.label;

  return structure;
};

// 获取主题配置单
const getThemeConfigure = function (configure) {
  return {
    name: configure.name,
    label: configure.label,
    desp: configure.desp,
    structure: configure.inventory.map((item) => mergeNodeStructure(item)),
  };
};

