import Vue from "vue";
import Vuex from "vuex";

import structure from "./structure";
import canvas from "./canvas";
import active from "./active";

import controls from "./controls";

import component from "./component";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    mode: "dev",
  },
  getters: {
    mode(state) {
      return state.mode;
    },
  },
  mutations: {
    setMode(state, mode) {
      state.mode = mode;
    },
  },
  actions: {},
  modules: {
    canvas,
    structure,
    controls,
    active,
    component,
  },
});
