import { components as shapeComponents } from "./shape";
import { components as widgetComponents } from "./widget";
import connectLine from "./connectLine.vue";
import { getComponentPropStructure } from "../../assets/js/utils";

const getNodeStructure = function (framework) {
  const structure = { ...getComponentPropStructure(connectLine) };
  framework.shape &&
    (structure.shape = getComponentPropStructure(
      shapeComponents[framework.shape]
    ));
  framework.markerStart &&
    (structure.markerStart = getComponentPropStructure(
      widgetComponents[framework.markerStart]
    ));
  framework.markerMid &&
    (structure.markerMid = getComponentPropStructure(
      widgetComponents[framework.markerMid]
    ));
  framework.markerEnd &&
    (structure.markerEnd = getComponentPropStructure(
      widgetComponents[framework.markerEnd]
    ));

  structure.framework = { ...framework };

  return structure;
};

const lineList = {
  basic: {
    shape: "linear",
    markerStart: "",
    markerMid: "textWidget",
    markerEnd: "arrow",
  },
};

export const structureList = [getNodeStructure(lineList.basic)];

console.log(structureList);

export const components = {
  connectLine,
};
