<template>
  <g>
    <circle
      :r="r"
      :style="cssStyle"
      :transform="`translate(${size.width / 2}, ${size.height / 2})`"
    >
    </circle>
    <image
      v-if="cssStyle.backgroundImage"
      :width="size.width"
      :height="size.height"
      :href="cssStyle.backgroundImage"
    ></image>
  </g>
</template>

<script>
export default {
  props: {
    size: {
      type: Object,
      required: true,
    },
    cssStyle: {
      type: Object,
      default: () => {
        return {
          fill: "red",
          stroke: "black",
          strokeWidth: 2,
          backgroundImage: "",
        };
      },
    },
  },

  computed: {
    r() {
      return Math.min(this.size.width, this.size.height) / 2;
    },
  },
};
</script>

<style lang="less" scoped></style>
