<template>
  <foreignObject :width="size.width" :height="size.height">
    <div class="from-box">
      <div
        v-show="!edit"
        class="mask"
        @dblclick="
          () => {
            edit = true;
          }
        "
      ></div>
      <textarea
        :style="cssStyle"
        :placeholder="configure.placeholder"
        v-model="configure.value"
        @blur="
          () => {
            edit = false;
          }
        "
      ></textarea>
    </div>
  </foreignObject>
</template>

<script>
export default {
  props: {
    size: {
      type: Object,
      required: true,
    },
    configure: {
      type: Object,
      default: () => {
        return {
          value: "",
          placeholder: "请输入文字",
        };
      },
    },
    cssStyle: {
      type: Object,
      default: () => {
        return {
          width: "100%",
          height: "100%",
          border: "0",
          color: "black",
          fontSize: "12px",
          padding: "0",
        };
      },
    },
  },

  data() {
    return {
      edit: false,
    };
  },
};
</script>

<style lang="less" scoped>
textarea {
  user-select: none;
}

.from-box {
  width: 100%;
  height: 100%;
  position: relative;
}

.mask {
  width: 100%;
  height: 100%;
  position: absolute;
  background: transparent;
}
</style>
