import Vue from "vue";
import { structureList as lineList } from "../components/line";
import { v4 as getUuid } from "uuid";

export default {
  namespaced: true,
  state: {
    nodes: {},
    lines: {},

    cacheLine: {
      start: "",
      end: "",
    },
  },
  getters: {
    nodes(state) {
      return state.nodes;
    },
    lines(state) {
      return state.lines;
    },
    getNode(state) {
      return (id) => state.nodes[id];
    },

    getCacheLine(state) {
      return state.cacheLine;
    },
  },
  mutations: {
    addNode(state, structure) {
      state.nodes[structure.id] = Vue.observable(structure);
      state.nodes.__ob__.dep.notify();
      console.log(state.nodes);
    },

    deleteNode(state, id) {
      Vue.delete(state.nodes, id);
    },

    addLine(state, structure) {
      state.lines[structure.id] = Vue.observable(structure);
      state.lines.__ob__.dep.notify();
    },

    generateLine(state) {
      if (state.cacheLine.start.node === state.cacheLine.end.node) {
        return;
      }
      const structure = Object.assign({}, lineList[0]);
      structure.start = { ...state.cacheLine.start };
      structure.end = { ...state.cacheLine.end };
      structure.id = getUuid();
      this.commit("structure/addLine", structure);
    },
  },
  actions: {},
};
