<template>
  <div class="canvasView-container" ref="container">
    <svg
      class="svg-container"
      :width="width"
      :height="height"
      @mousemove="mousemove"
      ref="svgCanvas"
    >
      <!-- line -->
      <component
        v-for="item in lines"
        :key="item.id"
        :is="item.component"
        v-bind="item"
      ></component>
      <!-- node -->
      <compnent
        ref="node"
        v-for="item in nodes"
        :key="item.id"
        :is="item.component"
        v-bind="item"
      ></compnent>

      <transfrom-controls
        ref="transformControls"
        v-if="dev"
        :show="transformControls.show"
        :target="transformControls"
      ></transfrom-controls>

      <selection-box v-if="dev"></selection-box>
    </svg>

    <svg class="svg-helper" v-if="dev" :width="width" :height="height"></svg>
  </div>
</template>

<script>
import selectionBox from "../components/interactive/selectionBox.vue";
import transfromControls from "../components/controls/transformControls.vue";
import { components as nodeComponents } from "../components/node";
import { components as lineComponents } from "../components/line";
export default {
  components: {
    selectionBox,
    transfromControls,
    ...nodeComponents,
    ...lineComponents,
  },
  data() {
    return {
      width: 0,
      height: 0,
    };
  },
  computed: {
    dev() {
      return this.$store.getters.mode === "dev";
    },
    nodes() {
      return this.$store.getters["structure/nodes"];
    },
    connectors() {
      return this.$store.getters["structure/connectors"];
    },
    lines() {
      return this.$store.getters["structure/lines"];
    },
    transformControls() {
      return this.$store.getters["controls/transform"];
    },
  },
  watch: {
    "$store.state.structure.nodes"(newValue) {
      if (newValue) {
        this.$nextTick(() => {
          const map = {};
          const nodesComponentList = this.$refs.node;
          nodesComponentList.forEach((component) => {
            map[component.id] = component;
          });
          this.$store.commit("component/cacheNodes", map);
        });
      }
    },
  },
  methods: {
    mousemove(event) {
      this.$store.commit("canvas/setPointer", {
        x: event.offsetX,
        y: event.offsetY,
      });
    },
  },
  mounted() {
    const container = this.$refs.container;
    this.width = container.offsetWidth;
    this.height = container.offsetHeight;

    // 初始画布事件
    const canvas = this.$refs.svgCanvas;
    canvas.addEventListener("click", () => {
      if (this.$refs.transformControls.changed) {
        this.$refs.transformControls.changed = false;
        return false;
      }
      this.$store.commit("controls/resetTransform");
      this.$store.commit("active/clearNode");
    });
    this.$store.commit("canvas/setCanvas", canvas);
  },
};
</script>

<style lang="less" scoped>
.canvasView-container {
  width: 100%;
  height: 100%;
  translate: relative;
}

.svg-container {
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.15) 10%,
      rgba(0, 0, 0, 0) 10%
    ),
    linear-gradient(to bottom, rgba(0, 0, 0, 0.15) 10%, rgba(0, 0, 0, 0) 10%);
  background-size: 10px 10px;
}

.svg-helper {
  pointer-events: none;
  background: transparent;
  position: absolute;
  top: 0;
  left: 0;
}
</style>
