<template>
  <g>
    <circle
      :r="configure.r"
      :style="cssStyle"
      :transform="`translate(${top.x}, ${top.y})`"
      @mousedown="mousedown('p1')"
      @mouseup="mouseup('p1')"
    ></circle>
    <circle
      :r="configure.r"
      :style="cssStyle"
      :transform="`translate(${right.x}, ${right.y})`"
      @mousedown="mousedown('p2')"
      @mouseup="mouseup('p2')"
    ></circle>
    <circle
      :r="configure.r"
      :style="cssStyle"
      :transform="`translate(${bottom.x}, ${bottom.y})`"
      @mousedown="mousedown('p3')"
      @mouseup="mouseup('p3')"
    ></circle>
    <circle
      :r="configure.r"
      :style="cssStyle"
      :transform="`translate(${left.x}, ${left.y})`"
      @mousedown="mousedown('p4')"
      @mouseup="mouseup('p4')"
    ></circle>
  </g>
</template>

<script>
export default {
  props: {
    node: {
      type: String,
      required: true,
    },
    position: {
      type: Object,
      required: true,
    },
    size: {
      type: Object,
      required: true,
    },
    configure: {
      type: Object,
      default: () => {
        return {
          r: 5,
          offset: {
            x: 0,
            y: 0,
          },
        };
      },
    },
    cssStyle: {
      type: Object,
      default: () => {
        return {
          fill: "white",
          stroke: "blue",
          strokeWidth: 1,
          cursor: "crosshair",
        };
      },
    },
  },
  data() {
    return {
      interface: ["p1", "p2", "p3", "p4"],
    };
  },

  computed: {
    cacheLine() {
      return this.$store.getters["structure/getCacheLine"];
    },

    top() {
      return {
        x: this.size.width / 2 + this.configure.offset.x,
        y: this.configure.offset.y,
      };
    },

    right() {
      return {
        x: this.size.width + this.configure.offset.x,
        y: this.size.height / 2 + this.configure.offset.y,
      };
    },

    bottom() {
      return {
        x: this.top.x,
        y: this.size.height + this.configure.offset.y,
      };
    },

    left() {
      return {
        x: this.configure.offset.x,
        y: this.right.y,
      };
    },

    p1() {
      return {
        x: this.position.x + this.top.x,
        y: this.position.y + this.top.y,
      };
    },

    p2() {
      return {
        x: this.position.x + this.right.x,
        y: this.position.y + this.right.y,
      };
    },

    p3() {
      return {
        x: this.position.x + this.bottom.x,
        y: this.position.y + this.bottom.y,
      };
    },

    p4() {
      return {
        x: this.position.x + this.left.x,
        y: this.position.y + this.left.y,
      };
    },
  },

  methods: {
    mousedown(point) {
      console.log(this.node, point, this[point]);
      this.cacheLine.start = {
        node: this.node,
        interface: point,
        position: { ...this[point] },
      };
    },
    mouseup(point) {
      console.log(this.node, point, this[point]);
      this.cacheLine.end = {
        node: this.node,
        interface: point,
        position: { ...this[point] },
      };
      this.$store.commit("structure/generateLine");
    },
  },
};
</script>

<style lang="less" scoped></style>
